
#svgContainer{display:none
}
#svgContainer *{stroke:inherit;fill:inherit
}
.icon-search{fill:transparent;stroke:#ccc
}
.btn-watchface svg,.btn-app svg{fill:transparent;stroke:#828682
}
.btn-watchface.active svg,.btn-app.active svg,.btn-watchface:hover svg,.btn-app:hover svg{fill:transparent;stroke:#FFF
}
.btn-download .icon-download{fill:#333;stroke:#ff4700
}
.icon-inverted-thumbs-up{fill:#FFF;stroke:#889097;position:relative;top:2px
}
.btn-thumbs-up .icon-thumbs-up{fill:#333;stroke:#ccc
}
.btn-thumbs-up:hover .icon-thumbs-up{fill:#ccc;stroke:#333
}
.btn-download:hover svg{fill:#ff4700;stroke:#333
}
.navbar-dark.bg-inverse.translucent a.search svg{height:22px;margin-top:3px
}
.navbar-dark.bg-inverse.translucent #categorySelector .btn-group .btn-outline-secondary.active svg.watchface #Clock,.navbar-dark.bg-inverse.translucent #categorySelector .btn-group .btn-outline-secondary:active svg.watchface #Clock,.navbar-dark.bg-inverse.translucent #categorySelector .btn-group .btn-outline-secondary:hover svg.watchface #Clock{stroke:#fff
}
.navbar-dark.bg-inverse.translucent #categorySelector .btn-group .btn-outline-secondary.active svg.app #App #use2,.navbar-dark.bg-inverse.translucent #categorySelector .btn-group .btn-outline-secondary.active svg.app #App #Line,.navbar-dark.bg-inverse.translucent #categorySelector .btn-group .btn-outline-secondary:active svg.app #App #use2,.navbar-dark.bg-inverse.translucent #categorySelector .btn-group .btn-outline-secondary:active svg.app #App #Line,.navbar-dark.bg-inverse.translucent #categorySelector .btn-group .btn-outline-secondary:hover svg.app #App #use2,.navbar-dark.bg-inverse.translucent #categorySelector .btn-group .btn-outline-secondary:hover svg.app #App #Line{stroke:#fff
}
.navbar-dark.bg-inverse.translucent #categorySelector .btn-group .btn-outline-secondary svg{margin-bottom:-6px;position:relative;top:-0.6px
}
@media screen and (max-width: 576px){
.apps .card-columns .card .card-text svg.thumbs-up{height:14px;top:3px
}
}
.apps .card-columns .card .card-text svg.thumbs-up{height:16px;margin-right:-6px;position:relative;top:2px
}
.app-details a.app-button div i{font-size:25px
}
@media screen and (max-width: 430px){
.app-button-container .btn svg{height:16px !important;margin-right:-5px;margin-bottom:-10px;top:-6px !important
}
}
.app-button-container .btn svg{height:20px;position:relative;top:2px
}
.app-button-container .btn.btn-thumbs-up:hover svg #Thumbs-Up,.app-button-container .btn.btn-thumbs-up:focus svg #Thumbs-Up,.app-button-container .btn.btn-thumbs-up.active svg #Thumbs-Up{stroke:#333
}
.app-button-container .btn.btn-thumbs-up:hover svg #Thumbs-Up #Shape,.app-button-container .btn.btn-thumbs-up:focus svg #Thumbs-Up #Shape,.app-button-container .btn.btn-thumbs-up.active svg #Thumbs-Up #Shape{fill:#ccc
}
.app-button-container .btn.btn-download:hover svg #Rectangle-10,.app-button-container .btn.btn-download:hover svg #Rectangle-11,.app-button-container .btn.btn-download:hover svg #Rectangle-8 #use2{stroke:#333
}
.app-button-container .btn.btn-download:hover svg #Rectangle-10,.app-button-container .btn.btn-download:hover svg #Rectangle-8 #use1{fill:#ff4700
}



