
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,400i");
@import url("https://fonts.googleapis.com/css?family=Raleway:400,400i,700,700i");
#app{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;display:-ms-flexbox;display:flex;min-height:100vh;-ms-flex-direction:column;flex-direction:column
}
body{font-family:'Raleway', sans-serif
}
h1,h2,h3,h4,h5,h6,small{font-family:'Open Sans', sans-serif;font-weight:400
}
p{font-family:'Raleway', sans-serif;font-weight:400
}
b{font-weight:700
}
body{background-color:#f4f3f4;display:-ms-flexbox;display:flex;min-height:100vh;-ms-flex-direction:column;flex-direction:column
}
body .flex-content{-ms-flex:1;flex:1
}
body .apps{margin-top:40px
}
.pull-right{float:right
}
.pebble{color:#ff4700 !important;display:inline
}
.tag-pebble{background-color:#ff4700
}
.btn-outline-pebble{margin-left:10px;color:#ff4700;border-color:#ff4700;background-image:none;background-color:transparent
}
.btn-outline-pebble:hover{background-color:#ff4700;color:#333
}
ul.pagination .page-item.active .page-link{background:#ff4700;border-color:#ff4700;color:#fff
}
ul.pagination .page-item.disabled .page-link{color:#818a91
}
ul.pagination .page-item .page-link{color:#ff4700
}
ul.pagination .page-item .page-link:hover,ul.pagination .page-item .page-link:focus{text-decoration:none;outline:none
}
header{padding-top:58px;background:linear-gradient(to bottom, #373a3c 0%, #373a3c 65%, rgba(55,58,60,0) 65%, rgba(55,58,60,0) 100%)
}
header .title-card{width:90vw;padding:20px;background-color:#fff;max-width:720px;max-height:320px;margin-left:auto;margin-right:auto;box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.2),0 1px 5px 0 rgba(0,0,0,0.12)
}
header .title-card h3{margin:0
}



